@import "./_variables.scss";

.App {
  > .logo {
    width: get-vh(190px);
    height: get-vh(71px);

    background: url("./assets/svg/logo.svg") no-repeat center center;
    background-size: cover;

    position: fixed;
    top: get-vh(33px);
    right: get-vh(62px);
  }

  > .Announcer {
    display: flex;
    position: fixed;
    top: get-vh(133px);
    right: get-vh(29px);

    > .announcements {
      display: flex;
      flex-direction: column;
      align-items: flex-end;

      > .announcement {
        display: flex;
        min-width: get-vh(453px);
        min-height: get-vh(94px);

        background: linear-gradient(90deg, rgba(37, 37, 37, 0.75) 0%, rgba(24, 69, 34, 0.75) 100%);
        border-radius: get-vh(70px) 0 0 get-vh(70px);

        justify-content: flex-end;
        align-items: center;

        padding-left: get-vh(30px);
        padding-right: get-vh(95px);

        position: relative;

        &:not(:first-child) {
          margin-top: get-vh(10px);
        }

        > svg.icon {
          width: get-vh(81px);
          height: get-vh(81px);
          position: absolute;
          top: get-vh(6px);
          right: get-vh(14px);
        }

        &:before {
          content: "";
          display: block;
          width: get-vh(5px);
          height: 100%;

          background: #63FF85;

          position: absolute;
          top: 0;
          right: 0;
        }

        > .wrap {
          display: flex;
          flex-direction: column;
          align-items: flex-end;

          position: relative;
          z-index: 1;

          > span:nth-child(1) {
            color: #fff;
            font-family: Akrobat-ExtraBold, sans-serif;
            font-weight: 800;
            font-size: get-vh(24px);
            line-height: get-vh(29px);

            text-transform: uppercase;
          }
          > span:nth-child(2) {
            color: rgba(255, 255, 255, 0.75);

            font-family: Akrobat-Bold, sans-serif;
            font-weight: bold;
            font-size: get-vh(14px);
            line-height: get-vh(17px);

            text-align: right;
            text-transform: uppercase;
          }
        }
      }
    }
  }

  > .Minimap {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    position: fixed;
    left: get-vh(334px);
    bottom: get-vh(34px);

    > .Notifier {
      display: flex;

      > .notifications {
        display: flex;
        flex-direction: column;

        > .notification {
          display: flex;
          width: get-vh(417px);
          height: get-vh(86px);

          align-items: center;

          border-radius: 0 get-vh(63px) get-vh(63px) 0;

          padding: 0 get-vh(50px) 0 get-vh(33px);

          position: relative;

          &.type-0 {
            background: linear-gradient(90deg, rgba(62, 22, 22, 0.75) 0%, rgba(37, 37, 37, 0.75) 77.6%);
          }
          &.type-1 {
            background: linear-gradient(90deg, rgba(49, 62, 22, 0.75) 0%, rgba(37, 37, 37, 0.75) 77.6%);
          }

          &:not(:first-child) {
            margin-top: get-vh(10px);
          }

          &:before {
            content: "";
            display: block;
            width: get-vh(6px);
            height: 100%;

            position: absolute;
            top: 0;
            left: 0;
          }
          &.type-0:before {
            background: #FF6363;
          }
          &.type-1:before {
            background: #CDFF63;
          }

          > .message {
            display: flex;
            flex-direction: column;
            align-items: flex-start;

            > span {
              text-transform: uppercase;
            }
            > span:nth-child(1) {
              color: #fff;

              font-family: Montserrat-ExtraBold, sans-serif;
              font-weight: 800;
              font-size: get-vh(18px);
              line-height: get-vh(22px);
              letter-spacing: 0.045em;
            }
            > span:nth-child(2) {
              color: rgba(255, 255, 255, 0.75);

              font-family: Montserrat-SemiBold, sans-serif;
              font-weight: 600;
              font-size: get-vh(12px);
              line-height: get-vh(15px);
              letter-spacing: 0.1em;
            }
          }

          > .icon {
            width: get-vh(20px);
            height: get-vh(20px);

            position: absolute;
            top: get-vh(15px);
            right: get-vh(25px);

            > circle {
              stroke-dashoffset: 937;
              animation: circleoff 5s;

              @keyframes circleoff {
                0% {
                  stroke-dashoffset: 1000;
                }
                100% {
                  stroke-dashoffset: 937;
                }
              }
            }
          }
        }
      }
    }

    > .money {
      display: flex;
      margin-top: get-vh(16px);

      > .item {
        display: flex;
        height: get-vh(37px);

        align-items: center;

        background: rgba(37, 37, 37, 0.75);

        border: get-vh(1px) solid rgba(37, 37, 37, 0.35);
        border-radius: get-vh(34px);

        padding: 0 get-vh(14px) 0 get-vh(7px);

        &:not(:first-child) {
          margin-left: get-vh(10px);
        }

        > .icon {
          display: flex;
          width: get-vh(23px);
          height: get-vh(23px);

          justify-content: center;
          align-items: center;

          border: get-vh(2px) solid #FFCB44;
          border-radius: 50%;

          > svg {
            width: get-vh(13px);
            height: get-vh(13px);
          }
        }

        > span {
          color: #fff;

          font-family: Montserrat-SemiBold, sans-serif;
          font-weight: 600;
          font-size: get-vh(18px);
          line-height: get-vh(22px);

          margin-left: get-vh(7px);

          > b {
            color: #CDFF63;
            font-family: Montserrat-SemiBold, sans-serif;
            font-weight: 600;
          }
        }
      }
    }

    > .additional {
      display: flex;
      margin-top: get-vh(16px);

      > .item {
        display: flex;
        height: get-vh(64px);

        align-items: center;

        border-radius: get-vh(50px);

        padding: 0 get-vh(23px) 0 get-vh(8px);

        &.location {
          background: linear-gradient(90deg, rgba(66, 66, 66, 0.75) 0%, rgba(37, 37, 37, 0.75) 77.6%);
        }
        &.thirst {
          background: linear-gradient(90deg, rgba(22, 47, 62, 0.75) 0%, rgba(37, 37, 37, 0.75) 77.6%);
        }
        &.food {
          background: linear-gradient(90deg, rgba(62, 50, 22, 0.75) 0%, rgba(37, 37, 37, 0.75) 77.6%);
        }

        &:not(:first-child) {
          margin-left: get-vh(10px);
        }

        > svg {
          width: get-vh(46px);
          height: get-vh(46px);
        }

        > .circle {
          display: flex;
          width: get-vh(46px);
          height: get-vh(46px);

          justify-content: center;
          align-items: center;

          position: relative;

          > svg {
            width: get-vh(46px);
            height: get-vh(46px);
          }

          > span {
            color: #fff;

            font-family: Montserrat-Bold, sans-serif;
            font-weight: bold;
            font-size: get-vh(13px);
            line-height: get-vh(16px);

            position: absolute;
          }
        }

        > .wrap {
          display: flex;
          flex-direction: column;
          align-items: flex-start;

          margin-left: get-vh(14px);

          > span {
            color: #fff;

            font-size: get-vh(14px);
            line-height: get-vh(17px);
          }
          > span:nth-child(1) {
            font-family: Montserrat-ExtraBold, sans-serif;
            font-weight: 800;
          }
          > span:nth-child(2) {
            font-family: Montserrat-SemiBold, sans-serif;
            font-weight: 600;
          }
        }
      }
    }
  }

  > .Speedometer {
    display: flex;
    width: get-vh(225px);
    height: get-vh(225px);

    position: fixed;
    right: get-vh(62px);
    bottom: get-vh(19px);

    > svg.speed,
    > svg.cursor {
      width: get-vh(225px);
      height: get-vh(225px);
      position: absolute;
      top: 0;
      left: 0;
    }

    > svg.fuel {
      width: get-vh(262px);
      height: get-vh(262px);
      position: absolute;
      top: get-vh(-18px);
      left: get-vh(-18px);
    }

    > .wrap {
      display: flex;
      width: get-vh(225px);
      height: get-vh(225px);

      flex-direction: column;
      align-items: center;

      padding-top: get-vh(55px);

      position: absolute;
      top: 0;
      left: 0;

      > span {
        font-family: Montserrat-Bold, sans-serif;
        font-weight: bold;
      }
      > span:nth-child(1) {
        color: rgba(255, 255, 255, 0.5);
        font-size: get-vh(14px);
        line-height: get-vh(17px);
      }
      > span:nth-child(2) {
        color: rgba(255, 255, 255, 0.25);
        font-size: get-vh(64px);
        line-height: get-vh(78px);

        > b {
          color: #fff;
        }
      }
    }
  }
}

.notification-enter {
  opacity: 0;
  transform: translateY(80px);
  transition: transform 300ms ease-in, opacity 300ms ease-in;
}
.notification-enter-active {
  opacity: 1;
  transform: translate(0px);
}

.notification-exit {
  opacity: 1;
  transform: translateY(0px);
  transition: transform 300ms ease-in, opacity 300ms ease-in;
}
.notification-exit-active {
  opacity: 0;
  transform: translateY(-80px);
}

.announcement-enter {
  opacity: 0;
  transform: translateY(-80px);
  transition: transform 300ms ease-in, opacity 300ms ease-in;
}
.announcement-enter-active {
  opacity: 1;
  transform: translateY(0px);
}
.announcement-exit {
  opacity: 1;
  transform: translateX(0px);
  transition: transform 300ms ease-in, opacity 300ms ease-in;
}
.announcement-exit-active {
  opacity: 0;
  transform: translateX(140px);
}